@import "../../../styles/helpers";

.section {
    overflow: hidden; }

.wrapper {
    position: relative; }

.title {
    margin-top: 32px;
    margin-bottom: 12px;
    @include d {
 }        //margin-bottom: 16px
    @include m {
        //margin-bottom: 16px
        text-align: center; } }

.subtitle {
    margin-bottom: 0px;
    font-size: 40px !important;
    font-weight: 100 !important;
    @include d {
 }        //margin-bottom: 16px
    @include m {
        //margin-bottom: 16px
        font-size: 30px !important;
        text-align: center; } }

.inner {
    margin: 0 -16px; }

.gallery {
    display: flex;
    flex-wrap: wrap;
    margin: -8px -4px 16px; }

.preview {
    flex: 0 0 calc(33.333% - 8px);
    width: calc(33.333% - 8px);
    margin: 8px 4px 0;
    &:first-child {
        flex: 0 0 calc(100% - 8px);
        width: calc(100% - 8px); }
    img {
        width: 100%;
        height: 450px;
        @include m {
            height: 100%; }
 } }        //border-radius: 8px

.subtitle {
    margin-bottom: 8px;
    width: max-content;
    @include m {
        width: inherit; }
    @include body-bold-1;
    color: $neutrals6;
    @include dark {
        color: $neutrals8; } }

.line {
    display: flex;
    align-items: center; }


.user {
    display: flex;
    @include m {
        display: block;
        text-align: center; }
    // display: flex
    text-align: center;
    margin-right: auto;
    color: $neutrals6;
    @include dark {
        color: $neutrals6; }
    span {
        font-weight: 500; } }

.avatar {
    flex-shrink: 0;
    width: 24px;
    height: 24px;
    margin-right: 12px;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%; } }

.counter {
    flex-shrink: 0; }

.head {
    max-width: 600px;
    margin-bottom: 64px;
    @include x {
        margin-bottom: 64px; }
    @include m {
        margin-bottom: 64px;
        display: center; } }

.section {
    padding-top: 32px; }

.heading {
    display: flex;
    @include m {
        display: contents; } }

.iconcircle {
    width: 100%;
    direction: rtl;
    padding-top: 36px;
    margin-bottom: 64px;
    @include x {
        margin-bottom: 64px; }
    @include m {
       display: none; } }
