    .slick-list {
        overflow: visible; }
    .slick-slide {
        padding: 0 12px;
        opacity: 0;
        transition: opacity .4s;
        &.slick-active {
            opacity: 1; } }
    .slick-arrow {
        bottom: 8px;
        background: $neutrals8;
        @include dark-common {
            background: transparent; } }
    .slick-prev {
        right: 324px;
        @include d {
            right: 276px; }
